.App {
  text-align: center;
  font-family: Arial, sans-serif;
}

h1 {
  margin: 20px 0;
}

.bingo-grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.bingo-row {
  display: flex;
}

.bingo-cell {
  width: 120px;
  height: 120px;
  border: 1px solid #000;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.bingo-cell.clicked {
  background-color: #90ee90;
  color: #fff;
}
